.projects-container {
  padding-block: 1rem;
  min-height: 100vh;
  @media (max-width: 1100px) {
    margin-block-start: 0.5rem;
  }

  @media (max-width: 600px) {
    margin-block-start: 1rem;
  }

  .project-skills {
    display: flex;
    align-items: center;

  }

  .projects-body {
    width: 95%;
    margin: auto;
    display: grid;
    grid-template-columns: repeat(2, minmax(300px, 450px));
    place-content: center;
    column-gap: 25px;
    padding-block: 2rem;

    @media (max-width: 1100px) {
      border: none;
      outline: none;
      background-color: transparent;
    }

    @media (max-width: 970px) {
      grid-template-columns: minmax(300px, 500px);
    }
  }
  .hide {
    opacity: 0;
    transition: opacity 0.6 ease;
  }

  .blob-bg {
    min-height: 750px;
    min-width: 550px;
    position: absolute;
    bottom: 0;
    left: 50%;
    // transform: translate(-65%, -50%);
    z-index: -1;
  }

  .more-projects {
    grid-column: 1 /2;
    width: 100%;
    display: flex;
    justify-content: center;
    padding-block: 1rem;
    button {
      padding: 1rem;
      font-size: 1.5rem;
    }
  }

}
