footer {
  align-items: center;
  gap: 0;
  flex-direction: column;
  color: whitesmoke;
  font-family: 'Montserrat', sans-serif;
  span {
    gap: 5px;
  }

  .skill-preview {
    img {
      width: 30px;
    }
  }

  @media (max-width: 500px) {
    font-size: 0.7rem;
  }

}
.contact {
  .contact-ways {
    justify-content: space-around;

  }
  width: 100%;
  img {
    height: 27px;
  }

  .gmail-img {
    margin-top: 5px;
  }
}
