
.container-2 {
  min-height: 350px;
  margin: 0 auto;
  padding: 1.3rem;
  border: 1px solid #ccc;
  outline: #2b2b2b4d 1px solid;
  text-align: center;
  overflow: hidden;
  transition: 0.6s;
  color: #1a1a1a;
  font-family: 'Montserrat', sans-serif;
  margin-top: 50px;
  box-shadow: 3px 3px 20px rgba(24, 24, 24, 0.244);
  .project-thumbnail {
    width: 280px;
    height: 90px;
    border-radius: 25px;
    background-position: center;
  }
  .number {
    margin: 20px auto;
    width: 60px;
    height: 60px;
    outline: 1px solid #ffb9c7;
    display: flex;
    justify-content: center;
    align-items: center;
    // color: #fff;
    // background-color: #d7d7d7;
    font: {
      size: 24px;
      weight: bold;
    }

    border-radius: 50%;
    transition: 0.7s;
  }
  h2 {
    margin: 20px 0;
    padding: 10px;
    font-size: 1.2em;
    text-transform: uppercase;
  }
  .text {
    line-height: 1.7;
    font: {
      // style: italic;
      size: 12.5px;
    }
    @media (max-width: 530px) {
      font-size: 14px;
    }
  }

  .project-skills {
    padding-block-end: 30px;

    img {
      max-height: 45px;
    }
  }

  .project-actions {
    display: flex;
    gap: 15px;
    transition: all 0.8s ease;
    justify-content: center;
    position: absolute;
    // right: 10px;
    bottom: 25px;
    left: 50%;
    transform: translateX(-50%);
    button {
      display: flex;
      justify-content: center;
      width: 110px;
    }
    .wrapper {
      position: relative;
      bottom: 50px;
      display: flex;
      gap: 8px;

      @media (max-width: 600px) {
        flex-direction: column;
        bottom: 320px;
      }
    }
  }
  @media (max-width: 1100px) {
    text-align: center;
    gap: 15px;
  }

  &:last-child {
    border-bottom: none;
  }

  &:first-child {
    padding-top: 0;
  }
}
