.education-body {
  .wrapper:not(:last-child) {
    border-bottom: 1px solid rgba(0, 0, 0, 0.14);
  }
  padding-block-start: 2rem;
  @media(max-width: 850px) {
    padding-block-start: 4rem;
  }
}

.education-container {
  min-height: 100vh;
}
.skills-intro {
  min-height: 250px;
  z-index: 0;
  justify-content: center;
  align-items: center;
  display: flex;
  padding-inline: 1rem;
  padding-block: 1rem;
  gap: 15px;
  font-size: 1rem;
  font-family: 'Montserrat', sans-serif;

  @media(max-width: 900px) {
    font-size: 0.95rem;
  }

  @media (max-width: 600px) {
    flex-direction: column;
    padding-inline: 0.5rem;

  }
  &.reverse {
    flex-direction: row-reverse;
    gap: 55px;
    img {
      top: 20px;
      left: 0;
    }

    @media(max-width: 600px) {
        flex-direction: column;
    }
  }
  p {
    line-height: 50px;
    max-width: 550px;
    position: relative;
    text-align: center;
  }

  img {
    max-height: 250px;
    position: relative;
    left: 80px;

    @media (max-width: 1150px) {
      left: 0;
    }

  }
}
