@keyframes slide-in-out {
  0% {
    transform: translateY(150%);
  }

  100% {
    transform: translateY(0);
    opacity: 1;
  }
}

@keyframes fadein {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

@keyframes fadeout {
  from {
    opacity: 1;
  }

  to {
    opacity: 0;
  }
}

@keyframes scalein {
  from {
    transform: scale(0.6);
  }

  to {
    transform: scale(1);
  }
}

@keyframes scaleout {
  from {
    transform: scale(1);
  }

  to {
    transform: scale(0.6);
  }
}

@keyframes scaleinsoft {
  from {
    transform: scale(0.8);
  }

  to {
    transform: scale(1);
  }
}

@keyframes bounce-0 {
  0%,
  20%,
  50%,
  80%,
  100% {
    transform: translateY(0);
  }
  40% {
    transform: translateY(-15px);
  }
  60% {
    transform: translateY(-7px);
  }
}


.slide-fade-off {
  transition: all 0.75s ease;
  transform: translateY(30%);
  opacity: 0;
}

.slide-fade-on {
  // animation: slide-in-out 1s, fadein 1s;
  transition: all 0.75s ease;
  transform: translateY(0);
  transition-delay: .8s;
  opacity: 1;
}

.page-animation-off {
  transition: max-height 4s ease-in;
  max-height: 200px;
  overflow: hidden;

}
.page-animation-on {
  transition: max-height 4s ease-in;
  // transition-delay: .5s;
  max-height: 1000vh;
}
