.about-container {
  // padding-inline: 10rem;
  // min-height: 100vh;
  color: rgb(238, 237, 237);
  display: flex;
  flex-direction: column;
  
  .skills-about {
    .skill-preview {
      padding: 0.2rem;
      outline: 3px solid rgba(178, 170, 185, 0.573);
      border-radius: 5px;
      border: 1.5px solid rgb(217, 217, 217);
      background-color: rgba(237, 237, 237, 0.69);
      display: flex;
      align-items: center;
    }
  }

  .about-header {
    margin-block-end: 2rem;
  }

  @media (max-width: 1100px) {
    padding-inline: 2rem;
    margin-block-start: 0.5rem;
  }

  .about-body {
    display: flex;
    justify-content: center;
    gap: 15px;
    font-size: 1.5rem;
    border-radius: 25px;
    flex-grow: 1;
    // padding: 1rem;
    @media (max-width: 800px) {
      font-size: 1.1rem;
    }

    .about-info {
      max-width: 500px;
      line-height: 35px;
      font-family: 'Lato';
      text-align: center;
    }

    .about-img {
      height: 300px;
      width: 300px;
      border-radius: 50%;
      background: url('https://gcdnb.pbrd.co/images/bWgLVYWD8SYr.png?o=1');
      background-size: contain;
      transition: all 0.4s ease;
      background-repeat: no-repeat;
      background-position: center;
      @media (max-width: 420px) {
        height: 250px;
        width: 250px;
      }
    }
  }
}

