.hero-wrapper {
  min-height: 100vh;
  display: flex;
  justify-content: center;
  cursor: default;
  gap: 50px;
  @media (max-width: 450px) {
    gap: 10px;
  }
  canvas {
    height: 500px;
    width: 500px;

    @media(max-width: 1300px) {
      max-width: 420px;
      max-height: 420px;
    }
  }

  @media (max-width: 1250px) {
    flex-direction: column;
  }

  @media (max-width: 740px) {
    .canvas-image {
      max-width: 350px;
      // position: relative;
    }
  }
  @media (max-width: 650px) {
    .canvas-image {
      max-width: 300px;
      position: relative;
    }
  }
  align-items: center;
}

.hero-container {
  font-family: 'Staatliches', sans-serif;
  color: black;
  // padding: 1rem;
  position: relative;

  @media (max-width: 1500px) {
    padding: 0;
  }

  .hero-title {
    // display: flex;
    justify-content: center;
    // transition: opacity 1s linear, transform 1s ease-in-out;
    font-size: 5rem;
    // text-align: center;
    line-height: 85px;
    color: whitesmoke;
    span.stroke-txt {
      position: relative;
      font-size: 6rem;
      @media(max-width: 750px) {
        font-size: 4.5rem;
      }
      @media(max-width: 600px) {
        font-size: 3.5rem;
      }
    }

    @media (max-width: 1600px) {
      font-size: 4.5rem;
      // line-height: 65px;
    }

    @media(max-width: 750px) {
      font-size: 4rem;
    }
    @media (max-width: 600px) {
      line-height: 55px;
      font-size: 3rem;
      justify-content: center;
    }
    @media (max-width: 450px) {
      justify-content: center;
      text-align: center;
    }
  }

  .hero-details {
    font-size: 1rem;
    font-family: 'Lato';
    display: flex;
    flex-direction: column;
    gap: 10px;
    -webkit-text-stroke: 0px;
    color: #f5f5f5;
    .mail-link {
      width: fit-content;

      @media(max-width: 600px) {
        align-self: center;

      }
    }

    span {
      line-height: 50px;
      @media (max-width: 800px) {
        font-size: 1.5rem;
      }

      @media (max-width: 600px) {
        font-size: 1rem;
      }
    }
  }
}
