nav {
  position: fixed;
  top: 0;
  right: 0;
  height: 100%;
  width: 150px;
  border-radius: 50% 0 0 50%;
  background-color: whitesmoke;
  clip-path: circle(25px at calc(100% - 45px) 45px);
  transition: all 0.3s ease-in-out;
  z-index: 10;
  border-left: 2px solid #414141;
  .contact {
    position: absolute;
    bottom: 20px;
    left: 25px;
  }

  @media (max-width: 900px) {
    width: 100px;
  }
}
#menuIcon:checked ~ nav {
  clip-path: circle(75%);
}
.menu-btn {
  position: fixed;
  right: 20px;
  top: 20px;
  height: 50px;
  width: 50px;
  text-align: center;
  line-height: 50px;
  border-radius: 50%;
  font-size: 20px;
  color: #000;
  cursor: pointer;
  background: transparent;
  transition: all 0.3s ease-in-out;
  z-index: 100;
  display: flex;
  flex-direction: column;
  justify-content: center;
  box-shadow: 1px 0px 11px 1px #ffffff94;

  span {
    background-color: black;
    border-radius: 10px;
    height: 4px;
    margin: 3px 5px;
    transition: 0.4s cubic-bezier(0.68, -0.6, 0.32, 1.6);
  }

  span:nth-of-type(1) {
    width: 45%;
  }

  span:nth-of-type(2) {
    width: 80%;
  }

  span:nth-of-type(3) {
    width: 65%;
  }
}
#menuIcon:checked ~ .menu-btn {
  color: #fff;
}

nav ul {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  list-style: none;
  text-align: center;
  display: flex;
  flex-direction: column;
  gap: 25px;
}
nav ul li {
  margin: 15px 0;
}
.nav-link {
  transition: 0.3s all;
  text-transform: uppercase;
  position: relative;
  padding-block-end: 3px;
  font-size: 0.85rem;
  font-weight: 100;
  // color: rgb(42, 42, 42);
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 5px;
  transition: all 0.2s ease;
  &.active {
    transition: all 0.2s ease;
    color: rgb(92, 92, 92);

    .nav-icon {
      transition: all 0.2s linear;
      filter: invert(88%) sepia(31%) saturate(2%) hue-rotate(256deg)
        brightness(70%) contrast(0%);
    }
  }

  @media (max-width: 900px) {
    font-size: 0.75rem;

    .nav-icon {
      max-height: 20px;
    }
  }
  @media (max-width: 600px) {
    padding-block-end: 0;
    margin-block-end: 1rem;
  }

  p {
    letter-spacing: 3px;

    @media (max-width: 450px) {
      letter-spacing: 1px;
    }
  }
  .nav-icon {
    height: 30px;
    width: 30px;
  }

  li {
    gap: 1.5px;
    // background: linear-gradient(to bottom, #661528, #490112);
    width: 100%;
  }
}

input[type='checkbox'] {
  display: none;
}

#menuIcon:checked ~ .menu-btn {
  span:nth-of-type(1) {
    transform-origin: bottom;
    transform: rotatez(45deg) translate(8px, 0px);
  }
}

#menuIcon:checked ~ .menu-btn {
  span:nth-of-type(2) {
    transform-origin: top;
    transform: rotatez(-45deg);
  }
}

#menuIcon:checked ~ .menu-btn {
  span:nth-of-type(3) {
    transform-origin: bottom;
    width: 50%;
    transform: translate(15px, -4px) rotatez(45deg);
  }
}
